export const apiDownloadDocumento = async (serviceUrl, id) => {
  const url = `${serviceUrl}/api/documento/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
    responseType: 'blob'
  };
  return requestBlob(url, options);
};

export const apiDownloadDocumentazione = async (serviceUrl, id) => {
  const url = `${serviceUrl}/api/documento/download/all?guid=${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
    responseType: 'blob'
  };
  return requestBlob(url, options);
};


export const requestBlob = async (url, options) => {
  const response = await fetch(url, options);
  if (response.status === 200) {
    console.log(response.headers.get('Content-Disposition'));
    const filename = response.headers.get('Content-Disposition').split('filename=')[1];
    response.blob().then(blob => {
      const otherUrl = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = otherUrl;
      a.download = filename;
      a.click();
    });
    return null;
  }
  return Promise.reject(response.status);
};


export const apiUploadDocumento = async (serviceUrl, key) => {
  return new Promise((resolve, reject) => {
    const input = document.createElement('input');
    input.type = 'file';
    input.onchange = (event) => {
      const file = event.path[0].files[0];
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file)
      fileReader.onload = () => {
        //const fileContent = fileReader.result.toString();
        const base64result = fileReader.result.substr(fileReader.result.indexOf(',') + 1);
        const slashIndex = file.type.indexOf("/");
        let fileType = slashIndex !== -1 ? file.type.substring(slashIndex + 1) : file.type;
        const uploadFileDto = {
          payload: base64result,
          mediaType: fileType,
          nomeDocumento: file.name,
          classeDocumentoId: key
        };
        const url = `${serviceUrl}/api/documento/upload`;
        console.log(uploadFileDto);
        const options = {
          ...getDefaultOptions(),
          method: 'POST',
          body: JSON.stringify(uploadFileDto)
        };
        request(url, options)
          .then(json => {
            console.log("ok")
            resolve()
          })
          .catch(err => {
            console.log(err);
            reject()
          });
      }
    }
    input.click();
  })
};
